import React from 'react'

const Logo = () => {
  return (
    <div>
        <div className="w-[80px] h-[80px] shadow-sm shadow-white rounded-full bg-violet-600 flex justify-center items-center text-white max-sm:w-[50px] max-sm:h-[50px] ">
        <h2 className="logo text-5xl font-extrabold max-sm:text-3xl">N</h2>
      </div>
    </div>
  )
}

export default Logo